.cardcont{
  max-width: 1500px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.p_button{
  height: 24px;
  width: 72px;
  margin: 6px;
  color: white;
  border-width: 0.5px;
  border-color: rgb(32, 32, 65);
  background-color: rgb(12, 12, 105);
  border-radius: 5px;
  font-size: 0.9em;
}
.p_button:hover{
  background-color: rgb(29, 29, 170);
  /* transform: scale(1.03); */
}

.op_btn_area{
  width: 8px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  color: white;
  border-width: 0.5px;
  border-color: rgb(32, 32, 65);
  background-color: rgb(12, 12, 105);
  border-radius: 5px;
  font-size: 0.9em;
}