.search_area{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sform{
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

hr.separate{
  width: 100%;
  border-color: rgb(129, 129, 129);
  border-style: solid;
  border-width: 0.1px;
}

.search_menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.search_btn{
  /* position: absolute; */
  height: 35px;
  margin: 6px;
  color: white;
  border-width: 0.5px;
  border-color: rgb(32, 32, 65);
  background-color: rgb(12, 12, 105);
  border-radius: 5px;
  font-size: 1em;

}
.search_btn:hover{
  background-color: rgb(37, 37, 163);
}



p {  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;}

